<template>
	<view style="background:#F5F5F5;padding: 10rpx 0rpx;font-size: 26rpx;color: #999999;font-family: PingFang SC;font-weight: 400;">
		<view  style="padding: 10rpx 30rpx;">基本信息</view>
		<view class="goodsTop">
			<view class="goodsName">
				<view>商品名称</view>
				<input v-model="formData.goods_name" type="text" value="" placeholder="请输入商品名称" />
			</view>
			<view class="goodsName" style="display: flex;justify-content: space-between;border-top:1px solid #f5f5f5">
				<view>商品分类</view>
				<view class="right" style="display: flex;align-items: center;">
					<text style="margin: 0 20rpx;">请选择</text>
					<image src="../../../static/leftarrow.png" mode="" style="width:17rpx;height:32rpx;"></image>
				</view>
			</view>
		</view>
		<!-- 商品主图 -->
		<view class="" style="margin-top:20rpx;background: #fff;display: flex;justify-content: space-between;align-items: center;padding: 10rpx 30rpx;">
		   <view class="">
		   	  <h2 style="color: #222222;font-size: 26rpx;font-family: PingFang SC;font-weight: 400;">商品主图</h2>
			  <p style="margin-top:10rpx;font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">1.建议比例1:1</p>
			  <p style="font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">2.为保证质量，单张大小不小于500KB</p>
		   </view>
		   <view class="" style="width: 120rpx;height: 120rpx;position: relative;">
		   	  <image @click="upimg"  :src="!formData.goods_icon?uploadImg:$cdnUrl + formData.goods_icon" style="width: 120rpx;height: 120rpx;" mode="aspectFill"></image>
			  <image @click="delGoods_icon" v-if="formData.goods_icon" src="../../../static/caca.png" mode="" style="width:26rpx;height:26rpx;position: absolute;right: -10rpx;top:-10rpx;"></image>
		   </view>
		</view>
		<!-- 上传视频  -->
		<view class="" style="margin-top:20rpx;background: #fff;display: flex;justify-content: space-between;align-items: center;padding: 10rpx 30rpx;">
		   <view class="">
		   	  <h2 style="color: #222222;font-size: 26rpx;font-family: PingFang SC;font-weight: 400;">上传视频（选填）</h2>
			  <p style="margin-top:10rpx;font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">1.建议比例16:9</p>
			  <p style="font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">2.为保证质量，建议大小为20MB</p>
		   </view>
		   <view class="" style="width: 120rpx;height: 120rpx;position: relative;">
		   	  <image v-if="!formData.goods_video" @click="upvideo"  :src="uploadVideo" style="width: 120rpx;height: 120rpx;" mode="aspectFill"></image>
			  <video v-if="formData.goods_video" :src="$cdnUrl + formData.goods_video" style="width: 120rpx;height: 120rpx;" controls></video>
			  <image @click="delGoods_video" v-if="formData.goods_video" src="../../../static/caca.png" mode="" style="width:26rpx;height:26rpx;position: absolute;right: -10rpx;top:-10rpx;"></image>
		   </view>
		</view>
		<!-- 视频封面 -->
		<view class="" style="background: #fff;display: flex;justify-content: space-between;align-items: center;padding: 10rpx 30rpx;">
		   <view class="">
		   	  <h2 style="color: #222222;font-size: 26rpx;font-family: PingFang SC;font-weight: 400;">上传视频封面（选填）</h2>
			  <p style="margin-top:10rpx;font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">1.建议比例1:1</p>
			  <p style="font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">2.为保证质量，单张大小不小于500KB</p>
		   </view>
		   <view class="" style="width: 120rpx;height: 120rpx;position: relative;">
		   	  <image @click="upVideoImg"  :src="!formData.goods_video_cover?uploadImg:$cdnUrl + formData.goods_video_cover" style="width: 120rpx;height: 120rpx;" mode="aspectFill"></image>
			  <image @click="delGoods_VideoImg" v-if="formData.goods_video_cover" src="../../../static/caca.png" mode="" style="width:26rpx;height:26rpx;position: absolute;right: -10rpx;top:-10rpx;"></image>
		   </view>
		</view>
		<!-- 商品相册 -->
		<view class="" style='background: #fff;padding: 15rpx 30rpx;margin-top: 10rpx;'>
			<h2 style="color: #222222;font-size: 26rpx;font-family: PingFang SC;font-weight: 400;">商品相册（最多上传5张）</h2>
			<p style="margin-top:10rpx;font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">1.建议比例1:1,建议尺寸大小：800*800px</p>
			<p style="font-size: 24rpx;font-family: PingFang SC;font-weight: 400;color: #999999;">2.为保证质量，单张大小不小于500KB</p>
		  <view class="" style="display: flex;flex-wrap: wrap;">
			  <view class="" v-for="(item,index) in formData.goods_img" style="margin-right:10rpx;width: 120rpx;height: 120rpx;position: relative;">
			  	<image   :src="$cdnUrl + item" style="width: 120rpx;height: 120rpx;" mode="aspectFill"></image>
				<image @click="delGoodsList(index)" src="../../../static/caca.png" v-if="item" mode="" style="width:26rpx;height:26rpx;position: absolute;right: -10rpx;top:-10rpx;"></image>
			  </view>
		  	  <view class="" v-if="formData.goods_img.length < 5">
		  	  	<image @click="upGoodsImgList"  :src="uploadImg" style="width: 120rpx;height: 120rpx;" mode="aspectFill"></image>
		  	  </view>
		  </view>
		</view>
		<!-- 金额设置 -->
		<view  style="padding: 5rpx 30rpx;">规格及库存</view>
		<view class="goodsTop">
		  <view class="goodsName" style="display: flex;align-items: center;">
		  	<view>市场价（元）</view>
		  	<input v-model="formData.goods_name" type="text" value="" placeholder="请输入" />
		  </view>
		  <view class="goodsName" style="display: flex;align-items: center;">
		  	<view>售价（元）</view>
		  	<input v-model="formData.goods_name" type="text" value="" placeholder="请输入" />
		  </view>
		  <view class="goodsName" style="display: flex;align-items: center;">
		  	<view>现金+积分</view>
		  	<input v-model="formData.goods_name" type="text" value="" placeholder="请输入" />
		  </view>
		</view>
	</view>
</template>

<script>
	
	export default {
		data() {
			return {
				uploadImg:'../../../static/uploadImg.png',
				uploadVideo:'../../../static/uploadVideo.png',
				formData:{
				  goods_name:'',
				  goods_icon:''	,
				  goods_video:'',
				  goods_video_cover:'',
				  goods_img:[],
				}
			}
		},
		methods: {
			//上传商品主图
			upimg(){
				var self = this;
				uni.showLoading({
				    title: '正在上传',
					mask:true
				});
				uni.chooseImage({
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        uni.uploadFile({
				            url: self.$baseUrl + '/ugo/api/mall.php?c=uploadCheckImage', //仅为示例，非真实的接口地址
				            filePath: tempFilePaths[0],
				            name: 'file',
				            success: (uploadFileRes) => {
								uni.hideLoading();
								self.formData.goods_icon =JSON.parse(uploadFileRes.data).data;
				            }
				        });
				    }
				});
			},
			//删除商品主图
			delGoods_icon(){
				this.formData.goods_icon = '';
			},
			//上传视频
			upvideo(){
				let self = this;
				uni.showLoading({
				    title: '正在上传',
					mask:true
				});
				uni.chooseVideo({  
				    success: (chooseVideo) => {
				        const tempFilePaths = chooseVideo.tempFilePath;
					    uni.uploadFile({
				            url: self.$baseUrl + '/ugo/api/mall.php?c=uploadCheckImage', //仅为示例，非真实的接口地址
				            filePath: tempFilePaths,
				            name: 'file',
				            success: (uploadFileRes) => {
								uni.hideLoading();
								self.formData.goods_video =JSON.parse(uploadFileRes.data).data;
				            },
							fail(err){
								console.log(err);
							}
				        });
				    }
				});
			},
			//删除视频
			delGoods_video(){
				this.formData.goods_video = ''
			},
			//视频封面
			upVideoImg(){
				var self = this;
				uni.showLoading({
				    title: '正在上传',
					mask:true
				});
				uni.chooseImage({
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        uni.uploadFile({
				            url: self.$baseUrl + '/ugo/api/mall.php?c=uploadCheckImage', //仅为示例，非真实的接口地址
				            filePath: tempFilePaths[0],
				            name: 'file',
				            success: (uploadFileRes) => {
								uni.hideLoading();
								self.formData.goods_video_cover =JSON.parse(uploadFileRes.data).data;
				            }
				        });
				    }
				});
			},
			//删除视频封面
			delGoods_VideoImg(){
				this.formData.goods_video_cover = '';
			},
			//上传商品相册
			upGoodsImgList(){
				var self = this;
				uni.showLoading({
				    title: '正在上传',
					mask:true
				});
				uni.chooseImage({
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        uni.uploadFile({
				            url: self.$baseUrl + '/ugo/api/mall.php?c=uploadCheckImage', //仅为示例，非真实的接口地址
				            filePath: tempFilePaths[0],
				            name: 'file',
				            success: (uploadFileRes) => {
								uni.hideLoading();
								self.formData.goods_img.push(JSON.parse(uploadFileRes.data).data);
								
				            }
				        });
				    }
				});
			},
			//删除商品相册列表
			delGoodsList(index){
				this.formData.goods_img.splice(index,1);
			}
		}
	}
</script>

<style lang="scss">
	.goodsTop{
		background-color: #fff;
		padding: 0 30rpx;
		.goodsName{
			   display: flex;
			   align-items: center;
			   padding: 20rpx 0;
			   view{
				   font-size: 26rpx;
				   font-family: PingFang SC;
				   font-weight: 400;
				   color: #333333;
			   }
			   input{
				   text-align: right;
				   flex: 1;
				   font-size: 26rpx;
				   font-family: PingFang SC;
				   font-weight: 400;
				   color: #999999;
			   }
		}
	}
</style>
